export function dateIndo(str) {
  const date = new Date(str);

  const tahun = date.getFullYear();
  const bulan = date.getMonth();
  const tanggal = date.getDate();

  let bulanIndo = '';

  switch(bulan) {
    case 0: bulanIndo = "Januari"; break;
    case 1: bulanIndo = "Februari"; break;
    case 2: bulanIndo = "Maret"; break;
    case 3: bulanIndo = "April"; break;
    case 4: bulanIndo = "Mei"; break;
    case 5: bulanIndo = "Juni"; break;
    case 6: bulanIndo = "Juli"; break;
    case 7: bulanIndo = "Agustus"; break;
    case 8: bulanIndo = "September"; break;
    case 9: bulanIndo = "Oktober"; break;
    case 10: bulanIndo = "November"; break;
    case 11: bulanIndo = "Desember"; break;
    default: bulanIndo = "not found";
   }

  return(`${tanggal} ${bulanIndo} ${tahun}`);
}