import { CCard, CCardBody, CCardHeader, CFormGroup } from "@coreui/react";
import React, { useEffect, useState } from "react";
import Header from "../../components/Header";
import FormInput from "../../components/FormInput";
import LoadingState from "../../components/LoadingState";
import DataTable from "../../components/DataTable";
import ActionButton from "../../components/ActionButton";
import { getUser, searchCyberTipline, deleteCyberTipline } from "../../api";
import { dateIndo } from "../../utils/formatter";
import ConfirmDialog from "../../components/ConfirmDialog";
import RenderForm from "../../components/RenderForm";

const CyberTiplineSearch = (props) => {
  const [startDate, setStartDate] = useState("");
  const [finishDate, setFinishDate] = useState("");
  const [ctNumber, setCtNumber] = useState("");
  const [createdBy, setCreatedBy] = useState("");
  const [users, setUsers] = useState([]);
  const [loadingApi, setLoadingApi] = useState(true);
  const [data, setData] = useState({});
  const [confirmDialog, setConfirmDialog] = useState(false);
  const [toDelete, setToDelete] = useState(null);
  const [loadingDelete, setLoadingDelete] = useState(false);
  const [showDataTable, setShowDataTable] = useState(false);
  const [maxStartDate, setMaxStartDate] = useState(null);
  const [minFinishDate, setMinFinishDate] = useState(null);

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const res = await getUser();
      let r = res.data.content.map((d) => ({ value: d.id, label: d.name }));
      setUsers(r);
    } catch (err) {}
    setLoadingApi(false);
  };

  const handleChangeStartDate = (d) => {
    setStartDate(d);
    setMinFinishDate(d);
  };

  const handleChangeFinishDate = (d) => {
    setFinishDate(d);
    setMaxStartDate(d);
  };

  // const formatDate = (value) => {
  //   const d = new Date(value);
  //   const m = d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
  //   const date = d.getDate() + 1 < 10 ? "0" + d.getDate() : d.getDate();
  //   return `${d.getFullYear()}-${m}-${date}`;
  // };

  const onSearch = async () => {
    setLoadingApi(true);
    const params = {};
    const isEmpty = (v) => v === "" || v === null || v === undefined;
    if (!isEmpty(startDate)) params.startDate = startDate;
    if (!isEmpty(finishDate)) params.finishDate = finishDate;
    if (!isEmpty(ctNumber)) params.ctNumber = ctNumber;
    if (!isEmpty(createdBy.value)) params.createdBy = createdBy.value;
    try {
      const res = await searchCyberTipline(params);
      formatData(res.data.content);
      setShowDataTable(true);
    } catch (err) {}
    setLoadingApi(false);
  };

  const formatData = (content) => {
    const columns = ["CT Number", "Priority", "Suspect Screen Name", "Suspect Email", "Suspect Phone", "Incident Date", ""];
    const rows = content.map((c) => [
      c.ctNumber,
      c.priority,
      c.suspectScreenName,
      c.suspectEmail,
      c.suspectPhone,
      dateIndo(c.incidentDate),
      actionBtn(c),
    ]);
    setData({ rows, columns });
  };

  const actionBtn = (d) => {
    const items = [
      { text: "Edit", onClick: () => props.history.push(`/cyber-tipline/edit?id=${d.id}`) },
      { text: "Detail", onClick: () => props.history.push(`/cyber-tipline/detail?id=${d.id}`) },
      { text: "Delete", onClick: () => openConfirmDialog(d) },
    ];
    return <ActionButton items={items} />;
  };

  const openConfirmDialog = (d) => {
    setConfirmDialog(true);
    setToDelete(d);
  };

  const fields = [
    <CFormGroup key="startDate">
      <label>Start Date</label>
      <input
        type="date"
        className="form-control"
        max={maxStartDate}
        value={startDate}
        onChange={(e) => handleChangeStartDate(e.target.value)}
      />
    </CFormGroup>,
    <CFormGroup key="finishDate">
      <label>finish Date</label>
      <input
        type="date"
        className="form-control"
        min={minFinishDate}
        value={finishDate}
        onChange={(e) => handleChangeFinishDate(e.target.value)}
      />
    </CFormGroup>,
    { label: "CT Number", value: ctNumber, onChange: (e) => setCtNumber(e.target.value), type: "text" },
    { label: "Created By", value: createdBy, onChange: (e) => setCreatedBy(e), type: "select", options: users },
  ];

  const handleDelete = async () => {
    setLoadingDelete(true);
    try {
      await deleteCyberTipline(toDelete.id);
      setConfirmDialog(false);
      await loadData();
    } catch (err) {}
    setLoadingDelete(false);
  };

  useEffect(() => {
    loadData();
  }, []);

  if (loadingApi) return <LoadingState />;
  return (
    <>
      <CCard>
        <CCardHeader>
          <Header title="Cyber Tipline Search" />
        </CCardHeader>
        <CCardBody>
          <div className="row">
            {fields.map((field, i) => (
              <div className={i < 2 ? "col-md-2" : "col-md-3"} key={i}>
                {React.isValidElement(field) ? (
                  field
                ) : (
                  <FormInput
                    label={field.label}
                    type={field.type}
                    value={field.value}
                    onChange={field.onChange}
                    options={field.options}
                  />
                )}
              </div>
            ))}
            <div className="col-md-2">
              <CFormGroup>
                <label htmlFor="">&ensp;</label>
                <button className="btn btn-secondary d-block" onClick={onSearch}>
                  Search
                </button>
              </CFormGroup>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              {showDataTable ? data.rows.length > 0 ? <DataTable data={data} /> : <h5>Data tidak ada!</h5> : null}
            </div>
          </div>
        </CCardBody>
      </CCard>
      <ConfirmDialog
        show={confirmDialog}
        onNegative={() => setConfirmDialog(null)}
        onPositive={handleDelete}
        btnLoading={loadingDelete}
      />
    </>
  );
};

export default CyberTiplineSearch;
